@import url('https://fonts.cdnfonts.com/css/neon');
*{
  font-family: 'Neon', sans-serif;
}
.App {
  text-align: center;
  background-color: #000000;
}

/* width */
::-webkit-scrollbar {
  width: 12px;
}

/* Track */
::-webkit-scrollbar-track {
  background:#000000
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #96b541; 
  border-radius: 3px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background:#b5af41; 
}

